import { layoutMenuPositionEffect } from '../effects/layoutEffects'
import { attrList, splitSymbol } from '../config'

const state = {
  menu: null,
  el: null,
  eventList: []
}

/**
 * ÈòªÊ≠¢ÈªòËÆ§‰∫ã‰ª∂ÂíåÂÜíÊ≥°
 * @param { Event } e ‰∫ã‰ª∂ÂèÇÊï∞
 * @returns { void }
 */
export const preventDefault = e => {
  // ÈòªÊ≠¢ÂÜíÊ≥°
  window.event ? window.event.cancelBubble = true : e.stopPropagation()
  // ÈòªÊ≠¢ÈªòËÆ§‰∫ã‰ª∂
  e.preventDefault ? e.preventDefault() : window.event.returnValue = false
}

/**
 * ÂàùÂßãÂåñËèúÂçïÊ†è
 * @param { Promise<object[]> | object[]} thenable ËèúÂçïÂàóË°®
 * @param { HTMLDivElement } el ÁªëÂÆöÊåá‰ª§ÁöÑÂÖÉÁ¥†
 * @param { Event } e ‰∫ã‰ª∂ÂèÇÊï∞
 * @returns { void }
 */
export const initMenu = async (thenable, el, e) => {
  // ÁªüËÆ°ÂºÇÊ≠•ÂàõÂª∫Ââç, ÊúâÊ≤°ÊúâÁÇπÂáª‰∫ã‰ª∂
  let flag = false
  const countClick = () => (flag = true)
  document.addEventListener('mousedown', countClick)
  // ÂºÇÊ≠•Ëé∑ÂèñÂà∞ËèúÂçïÈÖçÁΩÆÈ°π options
  const options = await Promise.resolve(thenable)
  // Ê∏ÖÈô§ÂºÇÊ≠•ÂâçÂàõÂª∫ÁöÑ‰∫ã‰ª∂
  document.removeEventListener('mousedown', countClick)
  // Â¶ÇÊûúÂºÇÊ≠•ÂâçÊúâÁÇπÂáªÊ¨°Êï∞, ÂàôÊâìÊñ≠ÈÄªËæë, ‰∏çÂàõÂª∫ËèúÂçï
  if (flag) return
  // ÂÖàÁßªÈô§‰πãÂâçÁöÑËèúÂçïÔºàËã•ÊúâÔºâ
  destroyMenu()

  // ÂºÄÂßãÂàõÂª∫ËèúÂçïÊ†è
  const menu = renderMenu(options)
  state.menu = menu
  state.el = el
  document.body.appendChild(menu)

  // ËÆ°ÁÆó‰∏ÄÁ∫ßËèúÂçïÊ†èÁöÑ‰ΩçÁΩÆ
  let x = e.clientX
  let y = e.clientY
  if (window.innerWidth - x < menu.offsetWidth) {
    x -= menu.offsetWidth
  }
  if (window.innerHeight - y < menu.offsetHeight) {
    y -= menu.offsetHeight
  }
  menu.style.left = x + 'px'
  menu.style.top = y + 'px'

  // Èò≤Ê≠¢ËèúÂçïÁªÑ‰ª∂ÈáåÁÇπÂá∫Á≥ªÁªüËèúÂçï
  menu.addEventListener('contextmenu', preventDefault)
  // Á™óÂè£ blur Êó∂ÈîÄÊØÅËèúÂçïÊ†è
  addEvent(window, 'blur', destroyMenu)
  // Á™óÂè£ resize Êó∂ÈîÄÊØÅËèúÂçïÊ†è
  addEvent(window, 'resize', destroyMenu)
  // È°µÈù¢ÁÇπÂáªÊó∂ÈîÄÊØÅËèúÂçïÊ†è
  addEvent(document, 'mousedown', clickPage)
}

/**
 * Ê∑ªÂä†‰∫ã‰ª∂
 * @param { Window | Document } target ÁõÆÊ†á‰∫ã‰ª∂Ê∫ê
 * @param { string } eventName ‰∫ã‰ª∂ÂêçÁß∞
 * @param { Function } callback ‰∫ã‰ª∂ÂõûË∞É
 * @returns { void }
 */
const addEvent = (target, eventName, callback) => {
  target.addEventListener(eventName, callback)
  state.eventList.push([target, eventName, callback])
}

/**
 * ÁßªÈô§ÊâÄÊúâ‰∫ã‰ª∂
 * @returns { void }
 */
const removeEvent = () => {
  while (state.eventList.length) {
    const [target, eventName, callback] = state.eventList.shift()
    target.removeEventListener(eventName, callback)
  }
}

/**
 * ÁÇπÂáªÈ°µÈù¢Êó∂ÈîÄÊØÅËèúÂçïÊ†è
 * @param { Event } e ‰∫ã‰ª∂ÂèÇÊï∞
 * @returns { void }
 */
const clickPage = e => {
  const hasMenu = e?.path?.some(node => node === state.menu)
  if (!hasMenu) destroyMenu()
}

/**
 * ÈîÄÊØÅËèúÂçïÊ†è
 * @returns { void }
 */
const destroyMenu = () => {
  const menuList = document.querySelectorAll('.vue-right-menu')
  // Ê∏ÖÈô§ÊâÄÊúâËèúÂçïÊ†è, ÊúâÂ§öÂ∞ëÊ∏ÖÂ§öÂ∞ë
  menuList && menuList.forEach(item => {
    item.parentNode.removeChild(item)
  })
  // ÁßªÈô§ÊâÄÊúâ‰∫ã‰ª∂
  removeEvent()
  state.el = null
  state.menu = null
}

/**
 * ËøáÊª§ÂêàÊ≥ïÁöÑ dom Â±ûÊÄß
 * @param { object } opt ËèúÂçïÁöÑitem
 * @returns { object }
 */
export const filterAttrs = (options, params) => {
  const res = {}
  attrList.forEach(key => {
    if (options[key]) {
      res[key] = options[key]
    }
    if (params[key]) {
      if (res[key]) {
        res[key] += (splitSymbol[key] + params[key])
      } else {
        res[key] = params[key]
      }
    }
  })
  return res
}

/**
 * Ê∏≤ÊüìËèúÂçïÊ†è
 * @param { object[] } options
 * @returns { HTMLDivElement }
 */

const renderMenu = (options) => {
  const cache = { hr: createHr, li: createLi, ul: createUl }
  const _state = {
    menu: null,
    el: null
  }
  const children = options.map(item => {
    try {
      return cache[item.type](item, _state)
    } catch (err) {
      throw new Error('Êú™Áü•ÁöÑ type Á±ªÂûã => ' + item.type)
    }
  })
  _state.menu = createDom('ul', { class: 'vue-right-menu' }, children)
  return _state.menu
}

/**
 * Ê∏≤Êüìdom
 * @param { String } [ tagName = 'ul' ] ÂÖÉÁ¥†ÂêçÁß∞
 * @param { Object } [ attrs = {} ] ÂÖÉÁ¥†Â±ûÊÄßÂØπË±°
 * @param { Array } [ children = [] ] Â≠êÂÖÉÁ¥†ÈõÜÂêà
 * @returns { HTMLDivElement }
 */
const createDom = (tagName = 'ul', attrs = {}, children = []) => {
  const el = document.createElement(tagName)
  // Âæ™ÁéØÊ∑ªÂä†Â±ûÊÄß
  Object.keys(attrs).forEach(key => {
    el.setAttribute(key, attrs[key])
  })
  // appendÊâÄÊúâÂ≠êÂÖÉÁ¥†
  // [TODO:]bug here  innerHTML ‰ºöÊ∏ÖÈô§‰πãÂâç append ÁöÑÊâÄÊúâ child
  children.forEach(child => {
    if (typeof child === 'string') {
      el.innerHTML += child
    } else if (child.nodeType === 1) {
      el.appendChild(child)
    }
  })
  return el
}

const createHr = opt => {
  const attrs = { class: 'menu-hr' }
  return createDom('li', filterAttrs(opt, attrs))
}

const createLi = opt => {
  const span = createDom('span', {}, [opt.text])
  const attrs = { class: opt.disabled ? 'menu-disabled' : '' }
  const li = createDom('li', filterAttrs(opt, attrs), [span])
  if (!opt.disabled && opt.callback) {
    li.addEventListener('mousedown', e => {
      opt.callback(e, state.el)
      destroyMenu()
    })
  }
  return li
}

const createUl = (opt, state) => {
  const span = createDom('span', {}, [opt.text])
  const attrs = { class: 'menu-list' + (opt.disabled ? ' menu-disabled' : '') }
  const li = createDom('li', filterAttrs(opt, attrs), [span])
  li._state = state
  // Ê∑ªÂä†‰∫åÁ∫ßËèúÂçï
  if (opt.children && opt.children.length) {
    const ul = renderMenu(opt.children)
    li.addEventListener('mouseover', e => {
      li.appendChild(ul)
      layoutMenuPositionEffect({
        baseEl: li,
        menu: ul
      })
    })
    li.addEventListener('mouseout', (e) => {
      if (!e.toElement) return
      let curr = e.toElement
      while (curr) {
        // Â¶ÇÊûúË∑ØÂæÑÈáåÂ≠òÂú® ul Ê†áÁ≠æ, Â∞±‰∏çÈúÄË¶ÅÈîÄÊØÅ
        if (curr === ul) return
        curr = curr.parentNode
      }
      li.removeChild(ul)
    })
  }
  return li
}
